html {
    box-sizing: border-box;
  }
  
  *,
  *::before,
  *::after{
    box-sizing: inherit;
  }

  body{
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    background: var(--light-background);
    color: var(--color-primary);
    font-size: 14px;
    line-height: 1.19;
    font-weight: 400;
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  ul {
    padding: 0;
    margin: 0;
  }
  
img{
  display: block;
  max-width: 100%;
  height: auto;
}
  
ul {
  list-style: none; 
}
  
.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}